function altDnsGen(wordList, dnsList, slim) {
  let generatedDns = [];
  let streamlined = false;
  if (slim === "slim") streamlined = true;
  let wordListLength = wordList.length,
    dnsListLength = dnsList.length;

  if (wordListLength === 0 || dnsListLength === 0) {
    alert("Word list and Domains can't be empty");
    return generatedDns;
  }
  /*   Below if else works for a .split(","),
   ** As empty input field would return an empty Array Length of 1 */
  if (wordListLength === 1 && wordList[0].trim() === "") {
    alert("Word list can't be empty");
    return generatedDns;
  }
  if (dnsListLength === 1 && dnsList[0].trim() === "") {
    alert("Domain Names can't be empty");
    return generatedDns;
  }
  /* If What Is In WordList Is Just PASS (in CAPITAL LETTERS),
   ** Then Just Return WordList Values */
  if (wordListLength === 1) {
    if (wordList[0] === "PASS") {
      generatedDns = dnsList;
      return generatedDns;
    }
  }

  function processDnsWildCardWithStar(subdomain, wordListElement) {
    // Process DNS With '*'

    let http = "";
    let www = "";

    // Split Domain Using // OR .
    let splitSubdomain = subdomain.split(/\/\/|\./g);

    // Sort Out http
    if (splitSubdomain[0].includes("http".toLocaleLowerCase())) {
      http = `${splitSubdomain[0]}//`;

      // Remove http From Array
      splitSubdomain.splice(0, 1);

      // Check & Sort www
      if (splitSubdomain[0] === "www".toLocaleLowerCase()) {
        www = "www.";

        // Remove www from Array
        splitSubdomain.splice(0, 1);
      }
    } else {
      // Check & Sort www
      if (splitSubdomain[0] === "www".toLocaleLowerCase()) {
        www = "www.";

        // Remove www from Array
        splitSubdomain.splice(0, 1);
      }
    }

    let httpWww = `${http}${www}`;

    let mapSplitSubdomain = splitSubdomain.map((identifier) => {
      if (identifier === "*") {
        identifier = wordListElement;
      }
      return identifier;
    });

    return `${httpWww}${mapSplitSubdomain.join(".")}`;
  }

  // Start altDnsGen Processing
  wordList.forEach((wordListElement) => {
    wordListElement = wordListElement.trim();

    if (wordListElement !== "") {
      dnsList.forEach((dnsListElement) => {
        dnsListElement = dnsListElement.trim();

        if (dnsListElement !== "") {
          if (streamlined) {
            if (dnsListElement.includes("*")) {
              // Process DNS With '*'
              let processedDnsWildCard = processDnsWildCardWithStar(
                dnsListElement,
                wordListElement
              );
              generatedDns.push(processedDnsWildCard);
            } else if (!dnsListElement.match(/^www\./i)) {
              // If dnsListElement Doesn't Start With www.
              //   ++++++++++++++++++++++++++++++++++++++++++++++
              if (!generatedDns.includes(dnsListElement)) {
                generatedDns.push(dnsListElement);
              }
              //   ++++++++++++++++++++++++++++++++++++++++++++++
              let sudoDns = wordListElement + "." + dnsListElement;
              generatedDns.push(sudoDns);
            } else {
              if (!generatedDns.includes(dnsListElement)) {
                generatedDns.push(dnsListElement);
              }

              // Add wordListElement After www.
              let splitDnsListElement = dnsListElement.split(".");
              splitDnsListElement.splice(1, 0, wordListElement);
              let newDns = splitDnsListElement.join(".");
              if (!generatedDns.includes(newDns)) {
                generatedDns.push(newDns);
              }
            }
          } else {
            if (dnsListElement.includes("*")) {
              // Process DNS With '*'
              let processedDnsWildCard = processDnsWildCardWithStar(
                dnsListElement,
                wordListElement
              );
              generatedDns.push(processedDnsWildCard);
            } else {
              let splitDnsListElement = dnsListElement.split(".");
              let splitDnsListElementLength = splitDnsListElement.length;
              splitDnsListElement.forEach((elem, index) => {
                //   Exclude The Authority Domain (e.g .com, .ng, .news etc)
                if (index !== splitDnsListElementLength - 1) {
                  let sudoSplitDnsListElement = [...splitDnsListElement];
                  //   Don't Put Anything Before "www." At The Start Of A URL
                  if (index === 0) {
                    if (!elem.match(/^www$/i)) {
                      //   ++++++++++++++++++++++++++++++++++++++++++++++
                      if (!generatedDns.includes(dnsListElement)) {
                        generatedDns.push(dnsListElement);
                      }
                      //   ++++++++++++++++++++++++++++++++++++++++++++++
                      sudoSplitDnsListElement[index] =
                        wordListElement + "." + elem;
                      let newDns = sudoSplitDnsListElement.join(".");
                      generatedDns.push(newDns);
                    } else {
                      let newDns = sudoSplitDnsListElement.join(".");
                      if (!generatedDns.includes(newDns)) {
                        generatedDns.push(newDns);
                      }
                    }
                  } else {
                    if (
                      splitDnsListElement[index - 1].toLowerCase() ===
                        "www".toLowerCase() &&
                      wordListElement.toLowerCase() === "www".toLowerCase()
                    ) {
                      // Leave Space Empty
                    } else {
                      sudoSplitDnsListElement[index] =
                        wordListElement + "." + elem;
                      let newDns = sudoSplitDnsListElement.join(".");
                      generatedDns.push(newDns);
                    }
                  }
                }
              });
            }
          }
        }
      });
    }
  });
  return generatedDns;
}

export default altDnsGen;
