import React from "react";
import { Link } from "react-router-dom";

function NotFound() {
  return (
    <React.Fragment>
      <nav
        className="navbar navbar-expand-lg fixed-top navbar-dark bg-dark"
        aria-label="Main navigation"
        style={{ width: "100%", height: "100%" }}
      >
        <div className=" row ps-4">
          <Link to="/" className="col clearfix mb-2">
            <button
              className="navbar-brand bg-dark"
              style={{ fontStyle: "italic", fontSize: "2.4em" }}
            >
              Subdomain Wildcard Locator.js
              <span
                className="float-end me-3"
                style={{
                  marginTop: "-0.08rem",
                  marginLeft: ".2rem",
                  color: "lightgray",
                  fontSize: "20px",
                }}
              >
                (01)
              </span>
            </button>
          </Link>
          <Link to="/altDnsApp" className="col clearfix mb-2">
            <button
              className="navbar-brand bg-dark"
              style={{ fontStyle: "italic", fontSize: "2.4em" }}
            >
              AltDns Generator.js
              <span
                className="float-end me-3"
                style={{
                  marginTop: "-0.08rem",
                  marginLeft: ".2rem",
                  color: "lightgray",
                  fontSize: "20px",
                }}
              >
                (02)
              </span>
            </button>
          </Link>
          <Link to="/altDnsApp" className="clearfix mb-2">
            <button
              className="navbar-brand bg-dark"
              style={{ fontStyle: "italic", fontSize: "2.4em" }}
            >
              DNS Resolver
              <span
                className="float-end me-3"
                style={{
                  marginTop: "-0.08rem",
                  marginLeft: ".2rem",
                  color: "lightgray",
                  fontSize: "20px",
                }}
              >
                (03)
              </span>
            </button>
          </Link>
          <Link to="/about" className="clearfix mb-2">
            <button
              className="navbar-brand bg-dark"
              style={{ fontStyle: "italic", fontSize: "2.4em" }}
            >
              About
              <span className="me-3"></span>
            </button>
          </Link>
        </div>
        {/* NotFound */}
        <div
          className="container col-md-6 me-4"
          style={{
            color: "lightgray",
            boxShadow:
              "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
          }}
        >
          <h4>
            <span className="text-danger display-4"> 404 </span>
            <br /> Page Not Found <hr />
          </h4>
          <p className="p-1">Sorry!!! The Page You Request Is Not Available.</p>
          <p className="small text-secondary pb-3">
            It might have been removed; had its name changed; or is temporarily
            unavailable. Please ensure the Web site address is spelled
            correctly, OR go to the <Link to="/">Home Page</Link>, OR use the
            Menu to navigate to a specific Page.
          </p>
        </div>
      </nav>
    </React.Fragment>
  );
}

export default NotFound;
