import React, { Component } from "react";

export default class FilterABModalHeader extends Component {
  clickedA = (e) => {
    e.preventDefault();

    // if (document.getElementById("returnedUniqueLetter").textContent === "B") {
    // console.log("Processing A");
    document.getElementById("clickedA").classList =
      "list-group-item active btn";
    document.getElementById("clickedB").classList =
      "list-group-item bg-secondary btn";
    document.getElementById("clickedAB").classList =
      "list-group-item bg-secondary btn";
    document.getElementById("returnedUniqueLetter").textContent = "A";
    document.getElementById("saveDiff2FileBadge").textContent = "A";

    let paramsA = document.getElementById("paramsA").value.split("\n");
    let paramsB = document.getElementById("paramsB").value.split("\n");
    document.getElementById("paramsABDifference").value = "";
    document.getElementById("returnedUniqueNum").textContent = "0";

    for (let i = 0; i < paramsA.length; i++) {
      const mainA = paramsA[i];

      if (!paramsB.includes(mainA)) {
        document.getElementById("paramsABDifference").value += `${mainA} \n`;
        document.getElementById("returnedUniqueNum").textContent =
          document.getElementById("returnedUniqueNum").textContent === ""
            ? (document.getElementById("returnedUniqueNum").textContent = 0)
            : (document.getElementById("returnedUniqueNum").textContent =
                Number(
                  document.getElementById("returnedUniqueNum").textContent
                ) + 1);
      }
    }
    // }
  };

  clickedB = (e) => {
    e.preventDefault();

    // if (document.getElementById("returnedUniqueLetter").textContent === "A") {
    console.log("Processing B");
    document.getElementById("clickedA").classList =
      "list-group-item bg-secondary btn";
    document.getElementById("clickedB").classList =
      "list-group-item active btn";
    document.getElementById("clickedAB").classList =
      "list-group-item bg-secondary btn";
    document.getElementById("returnedUniqueLetter").textContent = "B";
    document.getElementById("saveDiff2FileBadge").textContent = "B";

    let paramsA = document.getElementById("paramsA").value.split("\n");
    let paramsB = document.getElementById("paramsB").value.split("\n");
    document.getElementById("paramsABDifference").value = "";
    document.getElementById("returnedUniqueNum").textContent = "0";

    for (let i = 0; i < paramsB.length; i++) {
      const mainB = paramsB[i];

      if (!paramsA.includes(mainB)) {
        document.getElementById("paramsABDifference").value += `${mainB} \n`;
        document.getElementById("returnedUniqueNum").textContent =
          document.getElementById("returnedUniqueNum").textContent === ""
            ? (document.getElementById("returnedUniqueNum").textContent = 0)
            : (document.getElementById("returnedUniqueNum").textContent =
                Number(
                  document.getElementById("returnedUniqueNum").textContent
                ) + 1);
      }
    }
    // }
  };

  clickedAB = (e) => {
    e.preventDefault();

    // if (document.getElementById("returnedUniqueLetter").textContent === "A") {
    // console.log("Processing AB");
    document.getElementById("clickedA").classList =
      "list-group-item bg-secondary btn";
    document.getElementById("clickedB").classList =
      "list-group-item bg-secondary btn";
    document.getElementById("clickedAB").classList =
      "list-group-item active btn";
    document.getElementById("returnedUniqueLetter").textContent = "AB";
    document.getElementById("saveDiff2FileBadge").textContent = "AB";

    let paramsA = document.getElementById("paramsA").value.split("\n");
    let paramsB = document.getElementById("paramsB").value.split("\n");
    document.getElementById("paramsABDifference").value = "";
    document.getElementById("returnedUniqueNum").textContent = "0";

    let bothAB = [];
    for (let i = 0; i < paramsB.length; i++) {
      const mainB = paramsB[i];

      for (let i = 0; i < paramsA.length; i++) {
        const mainA = paramsA[i];
        if (!paramsB.includes(mainA)) {
          if (!bothAB.includes(mainA)) {
            bothAB.push(mainA);
            document.getElementById(
              "paramsABDifference"
            ).value += `${mainA} \n`;
            document.getElementById("returnedUniqueNum").textContent =
              document.getElementById("returnedUniqueNum").textContent === ""
                ? (document.getElementById("returnedUniqueNum").textContent = 0)
                : (document.getElementById("returnedUniqueNum").textContent =
                    Number(
                      document.getElementById("returnedUniqueNum").textContent
                    ) + 1);
          }
        }

        if (!paramsA.includes(mainB)) {
          if (!bothAB.includes(mainB)) {
            bothAB.push(mainB);
            document.getElementById(
              "paramsABDifference"
            ).value += `${mainB} \n`;
            document.getElementById("returnedUniqueNum").textContent =
              document.getElementById("returnedUniqueNum").textContent === ""
                ? (document.getElementById("returnedUniqueNum").textContent = 0)
                : (document.getElementById("returnedUniqueNum").textContent =
                    Number(
                      document.getElementById("returnedUniqueNum").textContent
                    ) + 1);
          }
        }
      }
    }
    // }
  };

  render() {
    return (
      <div className="container-fluid">
        {/* +++++++++++++++++++++++++++++ TOP ++++++++++++++++++++++++++++++++++++++ */}
        <nav
          className="navbar navbar-expand-lg navbar-nav-scroll navbar-dark bg-dark"
          aria-label="Main navigation"
        >
          <div className="container-fluid clearfix">
            <button
              className="navbar-brand bg-dark mx-auto"
              style={{ fontStyle: "italic", fontSize: "2.4em" }}
            >
              Filter Params A From B &amp; Return Unique Params
              <span
                className="float-end me-3"
                style={{
                  //   marginTop: "-0.08rem",
                  marginLeft: ".2rem",
                  color: "lightgray",
                  fontSize: "20px",
                }}
              >
                (05)
              </span>
            </button>
          </div>
        </nav>
        {/* ((((((((((((((((((((((((((((((((((((())))))))))))))))))))))))))))))))))))) */}
        <div
          className="nav-scroller bg-dark shadow-sm clearfix"
          style={{ marginTop: "0.1rem" }}
        >
          <nav className="nav nav-underline" aria-label="Secondary navigation">
            {/* ---------------------------------------------- */}
            <span class="list-group list-group-horizontal border border-1 mx-auto">
              <span
                id="clickedA"
                class="list-group-item active btn"
                onClick={this.clickedA}
              >
                <span style={{ fontSize: "1.5em" }}>A</span> &gt;&gt;&gt; B
              </span>
              <span
                id="clickedB"
                class="list-group-item bg-secondary btn"
                onClick={this.clickedB}
              >
                A &lt;&lt;&lt; <span style={{ fontSize: "1.5em" }}>B</span>
              </span>
              <span
                id="clickedAB"
                class="list-group-item bg-secondary btn"
                onClick={this.clickedAB}
              >
                A &gt;&gt;&gt; <span style={{ fontSize: "1.5em" }}>AB</span>
                &lt;&lt;&lt; B
              </span>
            </span>
            {/* ---------------------------------------------- */}
          </nav>
        </div>
        {/* ((((((((((((((((((((((((((((((((((((())))))))))))))))))))))))))))))))))))) */}
        <nav
          className="navbar navbar-expand-sm mb-2 clearfix"
          style={{
            backgroundColor: "limegreen",
            display: "block",
            marginTop: "-10px",
          }}
        >
          <h5
            style={{
              fontStyle: "italic",
              fontSize: "large",
              marginBottom: "-6px",
              marginLeft: "20px",
            }}
          >
            {" "}
            Returned{" "}
            <span
              id="returnedUniqueNum"
              style={{ fontWfontStyle: "italic", fontSize: "2em" }}
            ></span>{" "}
            Unique Count From{"  "}
            <span
              id="returnedUniqueLetter"
              style={{ fontStyle: "italic", fontSize: "1.7em" }}
            >
              A
            </span>{" "}
          </h5>
        </nav>
        {/* +++++++++++++++++++++++++++++ BOTTOM +++++++++++++++++++++++++++++++++++ */}
      </div>
    );
  }
}
