import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";

import {
  checkBoxGen,
  altDnsListGen,
  dnsInputListGen,
} from "../../../actions/altdnsActions/altDnsGeneratorActions";
import {
  initDisplay,
  readFile,
} from "../../utils/altdnsUtils/altDnsGeneratorFunctions";

import { setLocalStorage } from "../../utils/setLocalStorage";

import * as dns_Jhaddix from "../../utils/wordlist/dns-Jhaddix.txt";
import * as dns_namelist from "../../utils/wordlist/namelist.txt";
import * as dns_shubs_subdomains from "../../utils/wordlist/shubs-subdomains.txt";
import * as dns_subdomains_top1million_5000 from "../../utils/wordlist/subdomains-top1million-5000.txt";

class SideBar extends Component {
  state = {
    dns_Jhaddix_default: "",
    dns_namelist_default: "",
    dns_shubs_subdomains_default: "",
    dns_subdomains_top1million_5000_default: "",
  };

  // Abort Web Requests
  controller = new AbortController();
  /* Place signal In All Process That Needs To Be Ended.
   ** This Sends A Enables The controller To Close The Process On Abort() */
  signal = this.controller.signal;

  async componentDidMount() {
    if (JSON.parse(localStorage.getItem("altDnsCheckBox")) === true) {
      /* Await this.props.checkBoxGen()
       ** Because It's Value Would Be Used In Setting checkBox-input On Click In LS */
      await this.props.checkBoxGen();
      document.getElementById("checkBox-input").click();
    }

    await fetch(dns_Jhaddix.default, { signal: this.signal })
      .then((response) => response.text())
      .then((data) => {
        this.setState({ dns_Jhaddix_default: data });
      })
      .catch((e) => e);

    await fetch(dns_namelist.default, { signal: this.signal })
      .then((response) => response.text())
      .then((data) => {
        this.setState({ dns_namelist_default: data });
      })
      .catch((e) => e);

    await fetch(dns_shubs_subdomains.default, { signal: this.signal })
      .then((response) => response.text())
      .then((data) => {
        this.setState({ dns_shubs_subdomains_default: data });
      })
      .catch((e) => e);

    await fetch(dns_subdomains_top1million_5000.default, {
      signal: this.signal,
    })
      .then((response) => response.text())
      .then((data) => {
        this.setState({ dns_subdomains_top1million_5000_default: data });
      })
      .catch((e) => e);
  }

  componentWillUnmount() {
    this.controller.abort();
  }

  checkboxEvent = async () => {
    await this.props.checkBoxGen();

    let slim;
    if (this.props.checkBoxProps) {
      slim = "";
    } else {
      slim = "slim";
    }

    await this.props.altDnsListGen(
      this.props.wordListProps,
      this.props.dnsListProps,
      slim
    );
    await initDisplay(this.props.altDnsListProps);
    setLocalStorage("altDnsGen", this.props.altDnsListProps);
    setLocalStorage("altDnsCheckBox", this.props.checkBoxProps);
  };

  changeWordListEvent = async (e) => {
    let targetValue = e.target.value;

    let wordlist = "";

    if (targetValue === "... Select ...") {
      wordlist = "";
    } else if (targetValue === "dns-Jhaddix") {
      wordlist = this.state.dns_Jhaddix_default;
    } else if (targetValue === "namelist") {
      wordlist = this.state.dns_namelist_default;
    } else if (targetValue === "shubs-subdomains") {
      wordlist = this.state.dns_shubs_subdomains_default;
    } else if (targetValue === "subdomains-top1million-5000") {
      wordlist = this.state.dns_subdomains_top1million_5000_default;
    }

    document.getElementById("dnsWordListInput").value = "";
    document.getElementById("dnsWordListInput").value = wordlist;
  };

  formSubmitEvent = async (e) => {
    e.preventDefault();

    let getConfirmation = window.confirm("Generate AltDns Domains");

    if (getConfirmation) {
      // Hide "Result Not Saved TO Storage" RED Notification
      document.getElementById("notifyMeOfUnresolvedDns").style.display = "none";
      document.getElementById("notifyMeOfResolvedDns").style.display = "none";

      // Get Input Values & Place Inside An Array
      let dnsWordListInputValue =
        document.getElementById("dnsWordListInput").value;
      let dnsSplitWordListInputValue = dnsWordListInputValue.split(/,|\n/g);
      let dnsListInputValue = document.getElementById("dnsListInput").value;
      let dnsSplitListInputValue = dnsListInputValue.split(/,|\n/g);

      // Ensure Both Input Values Are Not Empty
      if (dnsWordListInputValue.trim() === "") {
        alert("Word-list can't be empty");
        return;
      }
      if (dnsListInputValue.trim() === "") {
        alert("Domain can't be empty");
        return;
      }

      // Monitor checkbox
      let slim;
      if (this.props.checkBoxProps) {
        slim = "";
      } else {
        slim = "slim";
      }

      await this.props.dnsInputListGen(
        dnsSplitWordListInputValue,
        dnsSplitListInputValue
      );

      await this.props.altDnsListGen(
        this.props.wordListProps,
        this.props.dnsListProps,
        slim
      );

      await initDisplay(this.props.altDnsListProps);

      // Save 2 Storage
      setLocalStorage("altDnsGen", this.props.altDnsListProps);
      setLocalStorage("altDnsWordList", dnsSplitWordListInputValue);
      setLocalStorage("altDnsDnsList", dnsSplitListInputValue);
      setLocalStorage("altDnsCheckBox", this.props.checkBoxProps);
    }
  };

  uploadWordListEvent = (e) => {
    e.preventDefault();
    readFile(e, "dnsWordListInput");
  };

  uploadDnsListEvent = (e) => {
    e.preventDefault();
    readFile(e, "dnsListInput");
  };

  GenerateWordList4RmPSWAL = (e) => {
    e.preventDefault();
    let getConfirmation = window.confirm(
      "Generate Word List From All Domains Generated In Dashboard"
    );

    // Fetch From LS & Not From props Bcs props Would Be Empty On Refresh Until WildcardApp componentDidMount
    let totalSubdomains = JSON.parse(
      localStorage.getItem("potentialWildcardLocator")
    ).totalSubdomains;

    if (getConfirmation) {
      document.getElementById("dnsWordListInput").value = "";
      // ++++++++++++++++++ Get Word List From Total Subdomains START ++++++++++++++++++
      let newSplitInputValue = [];

      totalSubdomains.forEach((element) => {
        let trimElement = element.trim();
        // Split By http
        trimElement = trimElement.split("://");

        trimElement.forEach((elem) => {
          if (elem !== "http" && elem !== "https") {
            // console.log("ELEM:", elem);
            let splitElem = elem.split(".");
            let splitElemLength = splitElem.length;
            // Remove Root Domain
            splitElem.splice(splitElemLength - 2, 2);
            // console.log("SPLITelem:", splitElem);

            splitElem.forEach((splitter) => {
              if (splitter !== "www") {
                // console.log("SPLITTER:", splitter);
                let splitterLength = splitter.length;
                let min = Number(localStorage.getItem("wordlistGenParamsMIN"));
                let max = Number(localStorage.getItem("wordlistGenParamsMAX"));
                let add2Max = Number(
                  localStorage.getItem("wordlistGenParamsAdd2Max")
                );

                // Process Wordlist Based  On Users Defined Min & Max Length
                if (
                  splitterLength >= min &&
                  splitterLength <= Number(max + add2Max)
                ) {
                  if (!newSplitInputValue.includes(splitter)) {
                    newSplitInputValue.push(splitter);
                  }
                }
              }
            });
          }
        });
      });
      newSplitInputValue.forEach((element) => {
        document.getElementById("dnsWordListInput").value += `${element}\n`;
      });
      // ++++++++++++++++++ Get Word List From Total Subdomains END +++++++++++++++++++
    }
  };

  uploadWildcard4RmPSWAL = (e) => {
    e.preventDefault();
    let getConfirmation = window.confirm("Upload WildCards From Dashboard");

    // Fetch Form LS & Not From props Bcs props Would Be Empty On Refresh Until WildcardApp componentDidMount
    let wildCard = JSON.parse(
      localStorage.getItem("potentialWildcardLocator")
    ).wildCardAreas;

    if (getConfirmation) {
      document.getElementById("dnsListInput").value = "";
      wildCard.forEach((element) => {
        document.getElementById("dnsListInput").value += `${element}\n`;
      });
    }
  };

  render() {
    let dnsWlPlaceholder = `
                      Enter DNS Word List
                                        or
                  PASS (to ignore wordlist)
  `;
    return (
      <form
        id="form"
        className="col-md-3 mb-2"
        style={{ backgroundColor: "rgb(197, 208, 219)" }}
        onSubmit={this.formSubmitEvent}
      >
        <div
          className="form-check-inline mx-auto"
          style={{
            fontWeight: "bolder",
            margin: "10px 25px 15px 25px",
            cursor: "pointer",
          }}
        >
          <label className="form-check-label" style={{ cursor: "pointer" }}>
            <input
              type="checkbox"
              id="checkBox-input"
              className="form-check-input me-2"
              style={{ cursor: "pointer" }}
              value=""
              onClick={this.checkboxEvent}
            />
            Check To Create All Possible AltDns Permutation
          </label>
        </div>

        <div className="form-group">
          <div
            className="input-group input-group-sm mb-4 pt-1"
            style={{ marginTop: "-1rem" }}
          >
            <label className="input-group-text" htmlFor="selectWordList">
              Select Word Lists
            </label>
            <select
              id="selectWordList"
              className="form-select bg-secondary text-light"
              onChange={this.changeWordListEvent}
            >
              <option defaultValue>... Select ...</option>
              <option value="dns-Jhaddix" style={{ color: "pink" }} disabled>
                dns-Jhaddix
              </option>
              <option value="namelist">namelist</option>
              <option value="shubs-subdomains">shubs-subdomains</option>
              <option value="subdomains-top1million-5000">
                subdomains-top1million-5000
              </option>
            </select>
          </div>
          <label htmlFor="dnsWordListInput" className="form-label h6">
            Input DNS Word List Here:
          </label>
          <textarea
            id="dnsWordListInput"
            className="form-control"
            rows="6"
            placeholder={dnsWlPlaceholder}
          ></textarea>
          <div className="form-group input-group-sm">
            <input
              id="uploadWordList"
              className="form-control border bg-secondary mt-1 mb-1"
              type="file"
              name="Word List"
              onChange={this.uploadWordListEvent}
            />
            <button
              id="GenerateWordList4RmPSWAL"
              type="button"
              className="mb-4 btn btn-secondary"
              onClick={this.GenerateWordList4RmPSWAL}
            >
              Generate Word List From Total Subdomains In Dashboard
            </button>
          </div>
        </div>

        <div className="form-group  pt-3">
          <label htmlFor="dnsListInput" className="form-label h6">
            Input Domains Here:
          </label>
          <textarea
            id="dnsListInput"
            className="form-control"
            rows="6"
            placeholder="                  Enter All DNS"
          ></textarea>
          <div className="form-group input-group-sm">
            <input
              id="uploadDnsList"
              type="file"
              className="form-control border bg-secondary mt-1 mb-1"
              name="DNS List"
              onChange={this.uploadDnsListEvent}
            />

            <button
              id="uploadWildcard4RmPSWAL"
              type="button"
              className="mb-4 btn btn-secondary"
              onClick={this.uploadWildcard4RmPSWAL}
            >
              Upload WildCards From Dashboard
            </button>
          </div>
        </div>

        <button
          type="submit"
          className="btn btn-success btn mb-2"
          style={{
            boxShadow:
              "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
          }}
        >
          Generate AltDns
        </button>
      </form>
    );
  }
}

SideBar.propType = {
  checkBoxGen: PropTypes.func.isRequired,
  checkBoxProps: PropTypes.bool.isRequired,
  wordListProps: PropTypes.array.isRequired,
  dnsListProps: PropTypes.array.isRequired,
};

const mapStateToProps = (state) => ({
  checkBoxProps: state.altDns.altDnsGenerator.checkBox,
  altDnsListProps: state.altDns.altDnsGenerator.altDnsList,
  wordListProps: state.altDns.altDnsGenerator.dnsInputList.wordList,
  dnsListProps: state.altDns.altDnsGenerator.dnsInputList.dnsList,
});

export default connect(mapStateToProps, {
  checkBoxGen,
  altDnsListGen,
  dnsInputListGen,
})(SideBar);
