import { saveAs } from "file-saver";
import React, { Component } from "react";
import { saveFile } from "./components/utils/altdnsUtils/altDnsGeneratorFunctions";
import { readFile } from "./components/utils/wildcardUtils/PotentialWildcardFunctions";
import FilterABModalHeader from "./FilterABModalHeader";

export default class FilterABModal extends Component {
  uploadFile4ParamsA = (e) => {
    e.preventDefault();

    readFile(e, "paramsA");
  };

  uploadFile4ParamsB = (e) => {
    e.preventDefault();

    readFile(e, "paramsB");
  };

  saveDiff2File = async () => {
    let paramsABDifference = await document
      .getElementById("paramsABDifference")
      .value.split("\n");

    saveAs(
      saveFile(
        paramsABDifference,
        `SWcLAltDnsGenUnique${
          document.getElementById("returnedUniqueLetter").textContent
        }Diffs.txt`
      )
    );
  };

  render() {
    return (
      // The Modal
      <div
        className="modal fade"
        id="filterABModal"
        data-bs-backdrop="static"
        data-bs-keyboard="false"
        tabIndex="-1"
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-xl modal-dialog-centered">
          <div className="modal-content" style={{ marginTop: "-1.7rem" }}>
            {/* Modal Header */}
            {/* <div className="modal-header"></div> */}
            {/* Modal body  */}
            <FilterABModalHeader />
            <div className="modal-body">
              <div className="row">
                <div className="col-sm-4">
                  <label htmlFor="paramsA" className="form-label h5">
                    Enter Params A
                  </label>
                  <textarea
                    id="paramsA"
                    className="form-control"
                    rows="9"
                    placeholder={"Params - A"}
                  ></textarea>
                  <input
                    id="uploadFile4ParamsA"
                    type="file"
                    className="border bg-secondary mt-1 mb-4"
                    onChange={this.uploadFile4ParamsA}
                  />
                </div>
                <div className="col-sm-4">
                  <label htmlFor="paramsB" className="form-label h5">
                    Enter Params B
                  </label>
                  <textarea
                    id="paramsB"
                    className="form-control"
                    rows="9"
                    placeholder={"Params - B"}
                  ></textarea>
                  <input
                    id="uploadFile4ParamsB"
                    type="file"
                    className="border bg-secondary mt-1 mb-4"
                    onChange={this.uploadFile4ParamsB}
                  />
                </div>
                <div className="col-sm-4">
                  <label htmlFor="paramsABDifference" className="form-label h5">
                    View Difference
                  </label>
                  <textarea
                    id="paramsABDifference"
                    className="form-control"
                    rows="9"
                    placeholder={"Params - A & B Difference"}
                    disabled
                  ></textarea>
                  <button
                    className="btn btn-primary btn-sm form-control mt-1"
                    style={{ fontStyle: "italic", fontSize: "1.1em" }}
                    onClick={this.saveDiff2File}
                  >
                    Save{" "}
                    <span
                      id="saveDiff2FileBadge"
                      className="badge bg-light text-dark rounded-pill align-text-bottom"
                    >
                      A
                    </span>{" "}
                    Difference To File
                  </button>
                </div>
              </div>
            </div>
            {/* Modal footer */}
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-danger me-5"
                data-bs-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
