import React, { Component } from "react";

export default class SetWordlistParamsModal extends Component {
  state = {
    defaultMin: "3",
    defaultMax: "15",
    defaultAdd2Max: "0",
  };
  // Manage Wordlist Generator Parameters
  componentDidMount() {
    if (!localStorage.getItem("wordlistGenParamsMIN")) {
      localStorage.setItem("wordlistGenParamsMIN", this.state.defaultMin);
      // Set Default Input Display Value
      document.getElementById("minWordLength").value = this.state.defaultMin;
    } else {
      document.getElementById("minWordLength").value = localStorage.getItem(
        "wordlistGenParamsMIN"
      );
    }

    if (!localStorage.getItem("wordlistGenParamsMAX")) {
      localStorage.setItem("wordlistGenParamsMAX", this.state.defaultMax);
      // Set Default Input Display Value
      document.getElementById("maxWordLength").value = this.state.defaultMax;
    } else {
      document.getElementById("maxWordLength").value = localStorage.getItem(
        "wordlistGenParamsMAX"
      );
    }

    if (!localStorage.getItem("wordlistGenParamsAdd2Max")) {
      localStorage.setItem(
        "wordlistGenParamsAdd2Max",
        this.state.defaultAdd2Max
      );
      // Set Default Input Display Value
      document.getElementById("add2MaxWordLength").value =
        this.state.defaultAdd2Max;
    } else {
      document.getElementById("add2MaxWordLength").value = localStorage.getItem(
        "wordlistGenParamsAdd2Max"
      );
    }
  }

  minWordLength = (e) => {
    e.preventDefault();

    // console.log(`MIN Input Changed To ${e.target.value}`);
    localStorage.setItem("wordlistGenParamsMIN", e.target.value);
  };

  maxWordLength = (e) => {
    e.preventDefault();

    // console.log(`MAX Input Changed To ${e.target.value}`);
    localStorage.setItem("wordlistGenParamsMAX", e.target.value);
  };

  add2MaxWordLength = (e) => {
    e.preventDefault();

    // console.log(`ADD2Max Input Changed To ${e.target.value}`);
    localStorage.setItem("wordlistGenParamsAdd2Max", e.target.value);
  };

  resetNum = (e) => {
    e.preventDefault();

    localStorage.setItem("wordlistGenParamsMIN", this.state.defaultMin);
    document.getElementById("minWordLength").value = localStorage.getItem(
      "wordlistGenParamsMIN"
    );

    localStorage.setItem("wordlistGenParamsMAX", this.state.defaultMax);
    document.getElementById("maxWordLength").value = localStorage.getItem(
      "wordlistGenParamsMAX"
    );

    localStorage.setItem("wordlistGenParamsAdd2Max", this.state.defaultAdd2Max);
    document.getElementById("add2MaxWordLength").value = localStorage.getItem(
      "wordlistGenParamsAdd2Max"
    );
  };

  render() {
    return (
      // The Modal
      <div
        className="modal fade"
        id="wordlistGenParamsModal"
        data-bs-backdrop="static"
        data-bs-keyboard="false"
        tabIndex="-1"
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered modal-dialog-scrollable">
          <div className="modal-content">
            {/* Modal Header */}
            <div
              style={{
                backgroundColor: "gray",
                border: "1px white solid",
              }}
              className="modal-header"
            >
              <h3>
                <em style={{ fontWeight: "400" }}>
                  Set Parameters For Generating Wordlist From Domain Names
                </em>
              </h3>
            </div>

            {/* Modal body  */}
            <div className="modal-body">
              <label
                style={{ fontWeight: "bold", fontSize: "large" }}
                htmlFor="minWordLength"
              >
                Min Word length To Return
              </label>{" "}
              <select
                id="minWordLength"
                name="minLength"
                style={{
                  marginBottom: "5px",
                  fontWeight: "bold",
                  fontSize: "large",
                  borderRadius: "50px 15px 50px 15px",
                  paddingLeft: "13px",
                }}
                onChange={this.minWordLength}
              >
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
                <option value="5">5</option>
                <option value="6">6</option>
                <option value="7">7</option>
                <option value="8">8</option>
                <option value="9">9</option>
                <option value="10">10</option>
              </select>
              <label
                style={{
                  fontWeight: "bold",
                  fontSize: "large",
                }}
                htmlFor="maxWordLength"
              >
                Max Word length To Return
              </label>{" "}
              <select
                id="maxWordLength"
                name="maxLength"
                style={{
                  marginBottom: "5px",
                  fontWeight: "bold",
                  fontSize: "large",
                  borderRadius: "50px 15px 50px 15px",
                  paddingLeft: "13px",
                }}
                onChange={this.maxWordLength}
              >
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
                <option value="5">5</option>
                <option value="6">6</option>
                <option value="7">7</option>
                <option value="8">8</option>
                <option value="9">9</option>
                <option value="10">10</option>
                <option value="11">11</option>
                <option value="12">12</option>
                <option value="13">13</option>
                <option value="14">14</option>
                <option value="15">15</option>
              </select>
              <span
                style={{
                  marginLeft: "0.5rem",
                  fontWeight: "bolder",
                  fontSize: "larger",
                }}
              >
                {" "}
                +{" "}
              </span>
              <input
                id="add2MaxWordLength"
                type="number"
                min="0"
                placeholder="0"
                style={{
                  marginLeft: "0.5rem",
                  fontWeight: "bold",
                  fontSize: "large",
                  width: "15%",
                  borderRadius: "50px 15px 50px 15px",
                  paddingLeft: "13px",
                }}
                // value={localStorage.getItem("wordlistGenParamsAdd2Max")}
                onChange={this.add2MaxWordLength}
              />
            </div>

            {/* Modal footer */}
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-warning me-auto"
                onClick={this.resetNum}
              >
                Reset
              </button>

              <button
                type="button"
                className="btn btn-danger"
                data-bs-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
